.loading-inline {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  display: inline-block;

  text-align: center;
  color: transparent;

  opacity: 0;
  transform: translateZ(0); // Jitter...
  transition: opacity 1ms linear;

  &.visible {
    opacity: 1;
    transition: opacity 1000ms ease-out;
  }
}

