* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  width: 100%;
  font-size: 10px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
#app,
.app,
.pt-portal,
.pt-overlay {
  font-size: 1.4rem;
}
#app,
.app {
  height: 100%;
  width: 100%;
}
.grid {
  height: 100%;
  width: 100%;
}
.grid h1 {
  margin: 2rem 0;
}
main {
  position: relative;
  overflow: auto;
  padding: 0 1.5rem 6rem;
}
footer {
  background-color: #000032;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.devnote {
  font-style: italic;
  font-weight: 100;
  color: #888;
  font-size: 1.2rem;
  margin: 2rem 0;
}
.devnote strong {
  color: #444;
}
body.bp3-dark {
  background-color: #30404d;
}
.navbar {
  display: flex;
  background-color: #30404d;
  align-items: center;
  justify-content: space-between;
  padding-right: 1rem;
}
.navbar * {
  color: white;
}
.admin {
  height: 100%;
  width: 100%;
}
.admin .header [type="file"] {
  visibility: hidden;
  width: 0;
  max-width: 0;
}
.admin .archived-button {
  float: right;
}
.admin .admin-heading {
  margin-bottom: 1.5rem;
}
.admin .admin-heading .bp3-icon {
  margin-right: 0.5rem;
}
.admin .admin-heading button {
  margin-left: 1rem;
}
.admin .bp3-callout {
  display: inline-block;
  width: auto;
}
.admin .double-stripped > tbody > tr:nth-child(4n + 1) > td,
.admin .double-stripped > tbody > tr:nth-child(4n + 2) > td {
  background-color: rgba(191, 204, 214, 0.15);
}
.admin .double-stripped > tbody .batch-games-table {
  width: 100%;
}
.admin .double-stripped > tbody .batch-games-table tbody tr:first-child th {
  box-shadow: none !important;
}
.admin .double-stripped > tbody .batch-games-table .player-group {
  margin-right: 8px;
}
.admin .double-stripped > tbody .detailsButton {
  width: 12px;
  transform: rotate(-90deg);
}
.admin .double-stripped > tbody .detailsButton.detailsVisible {
  transform: rotate(0);
}
.admin main {
  overflow: auto;
  padding: 1.5rem 1.5rem 7rem;
}
.admin .games,
.admin .players {
  height: calc(100vh - 14rem);
}
.admin .inner-table tbody tr td {
  box-shadow: none !important;
}
.admin .button-group {
  display: flex;
}
.admin .add-treatment-popover {
  padding: 1rem;
}
.admin .factors .factors-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(40rem, 1fr));
  grid-gap: 3rem;
}
.admin .factor h4 {
  margin-top: 0;
}
.admin .factor .factor-actions {
  float: right;
}
.admin .required label:not(.bp3-radio)::after {
  content: "*";
  position: relative;
  left: 0.2em;
  color: red;
}
.admin .bp3-html-select,
.admin .bp3-html-select select {
  max-width: 100%;
}
.admin table tr td.numeric {
  text-align: right;
}
.core {
  padding: 1rem 2rem;
}
.round-nav {
  padding: 1rem 0;
}
.round-nav .bp3-breadcrumb.bp3-breadcrumb-current {
  padding: 0.2rem 2rem;
  border-radius: 3px;
  background-color: #106ba3;
  color: white;
  -webkit-font-smoothing: antialiased;
}
.centered > * {
  margin: 0 auto;
  width: 102.4rem;
  max-width: 100%;
}
.consent button {
  width: 16rem;
}
.delayed {
  opacity: 0;
  transform: translateZ(0);
  transition: opacity 1ms linear;
  height: inherit;
  width: inherit;
}
.delayed.visible {
  opacity: 1;
  transition: opacity 500ms ease-out;
}
.game {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.game .round {
  flex-grow: 1;
}
.finished {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.game.finished,
.game.waiting {
  height: calc(100vh - 19rem);
  padding: 0;
}
.game-lobby {
  height: calc(100vh - 19rem);
}
.help-tooltip-icon {
  color: #c8c8c8;
  margin: 0 0.6rem;
}
.help-tooltip {
  display: block;
  width: 35rem;
  white-space: normal;
}
.help-tooltip-wrapper {
  display: inline-block !important;
}
.loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: transparent;
  opacity: 0;
  transform: translateZ(0);
  transition: opacity 1ms linear;
}
.loading.visible {
  opacity: 1;
  transition: opacity 2000ms ease-out;
}
