  }
}
.login {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login form {
  width: 35rem;
}
.login > p {
  margin: 2rem 0;
  padding: 0 1rem;
  max-width: 35rem;
}
.no-batch {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.no-batch p {
  max-width: 50ch;
  text-align: center;
}
.new-player input {
  width: 24rem;
}
.new-player button {
  width: 16rem;
}
